*{
  margin:0;
  padding:0;
}

#root{
  display: flex;
  flex-direction: column;
  align-items: center;
}


#App{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  width: max(80vw, 500px);
}
#header{
  margin-top: 1rem;
  display: grid;
  grid-template-columns:  1fr 2fr 1fr;
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas: 
  'description title score'
  'description title stage'
  'description title bestStage';
  column-gap: 1rem;
}

#header>button{
  grid-area: description;
  background:none;
  border: none;
  cursor: pointer;
}
#header>button:hover{
  color: #ff0000;
}

#description{
  position: absolute;
  width: min(80vw, 12rem);
  top:100px;
  background: white;
  border: 1px solid black;
  padding: 1em;
}

#gameTitle{
  grid-area: title;
  align-self: center;
  justify-self: center;
  font-size: 3rem;
}
#score{
  grid-area: score;
}
#stage{
  grid-area: stage;
}
#bestStage{
  grid-area: bestStage;
}

#cardsContainer{
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(3,min(200px, 20vw));
  grid-template-rows: repeat(3, min(200px, 20vw));
  column-gap: 1rem;
  row-gap: 1rem;
}